.add-skills {
  max-width: 376px;
  margin-bottom: 44px;
}

.search-input {
  margin-bottom: 12px;
}
.search-input input {
  width: 100%;
  height: 48px;
  padding-left: 50px;
  margin-bottom: 0;
}
.search-input .search-input__input > button {
  top: 50%;
  z-index: 2;
  left: 13px;
  transform: translateY(-50%);
  position: absolute;
  background-color: transparent;
  border: 0;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  padding: 0;
}
.search-input .search-input__input button:hover {
  background-color: #F1F6FA;
}

.add-skills__tags {
  display: flex;
  flex-wrap: wrap;
}

.add-skills__tag {
  padding: 4px 10px;
  background: #DBEEFB;
  border-radius: 40px;
  font-weight: bold;
  font-size: 10px;
  line-height: 1.4;
  display: flex;
  align-items: center;
  color: #3C64B1;
  margin-right: 7px;
  margin-bottom: 10px;
}
.add-skills__tag button {
  background-color: transparent;
  border: 0;
  padding: 0;
  margin-left: 10px;
}

.exp-content__skills {
  margin-top: 80px;
  margin-bottom: 80px;
}

@media (max-width: 767px) {
  .exp-content__skills {
    margin-top: 25px;
    margin-bottom: 50px;
  }
}
.search-input {
  position: relative;
}

.search-input__input {
  position: relative;
}

.search-input__input input:focus {
  border: 1px solid #2B5CFB;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 20px 20px 0px 0px;
  outline: none;
}

.search-input__list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  max-height: 300px;
  overflow: auto;
  z-index: 3;
  background-color: #fff;
  border: 1px solid #2B5CFB;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 0px 0px 4px 4px;
  margin: 0;
  padding: 0;
  display: none;
}

.search-input.focus .search-input__list {
  display: block;
}

.search-input__list li a {
  font-size: 14px;
  line-height: 24px;
  color: #8B90A0;
  margin: 0;
  padding: 0;
  padding: 8px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  display: block;
}

.search-input__list li {
  list-style: none;
}

.search-input__list li a:hover {
  color: #092C9E;
  background-color: rgba(0, 0, 0, 0.05);
}